.exerciseHeading {
    text-align: center;
}
.cardBorder {
    width: 210px;
    border: 3px solid black;
    border-radius: 5%;
    margin: auto;
    
    
}
.cardImage {
    
    width: 190px;
    border-radius: 50%;
    border: 3px solid #f4978e;
    display: block;
    margin: auto;
    margin-top: 5px;
}
.cardText {
    text-align: center;
    font-size: x-large;
    font-weight: 700;
}
.exerciseButton {
    display: block;
    margin: auto;
    background-color: #14213d;
    border: 2px solid;
    color: #ffa260;
    padding: 1em 2em;
    font-size: 1em;
    transition: all 0.025s;
    
}
.exerciseButton:hover {
background-color: none;
border-color: #f1ff5c;
color: white;
box-shadow: 0 0.5em 0.5em -0.4em #f1ff5c;
transform: translateY(-0.25em) ;
cursor: pointer;
}

.dark-background {
background-color: #14213d;
height: 75px;
padding-top: 15px;
}